.contacts {
    padding-bottom: 113px;
    background-image: linear-gradient(
        to bottom, 
        var(--primary-white-color),
        var(--primary-white-color) 102px,
        var(--secondary-bg-color) 102px,
        var(--secondary-bg-color) 100%);
    @include mq(tablet) {
        padding-bottom: 158px;
    }
    @include mq(desktop) {
        padding-bottom: 165px;
        background-image:
        url('../images/contacts/contacts-bg-lg.png'),
        linear-gradient(
            to bottom, 
            var(--secondary-bg-color),
            var(--secondary-bg-color));
        @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
        background-image: 
        url('../images/contacts/contacts-bg-@2x-lg.png'),
        linear-gradient(
            to bottom, 
            var(--secondary-bg-color),
            var(--secondary-bg-color)),
            
        }
        background-repeat: no-repeat;
        background-position: 0 102px;
        background-size: contain;
        }
}

.contacts__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-bottom: 54px;
    @include mq(mobile-only) {
        flex-direction: column;
    }
    @include mq(tablet) {
        flex-direction: row;
        margin-left: -20px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        
    }
    @include mq(desktop) {
        margin-left: -30px;
    }
}

.contacts__item {
    padding: 43px 20px;
    
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;
    background-color: var(--primary-white-color);
    @media screen and (max-width: 480px) {
        max-width: 220px;
    }
    @include mq(mobile) {
        max-width: 350px;
    }
    @include mq(tablet) {
        padding: 50px 31px;
        margin-left: 20px;
        flex-basis: calc((100% - 60px) / 3);
        
    }
    @include mq(desktop) {
        padding: 51px 44px 62px 43px;
        
        margin-left: 30px;
        flex-basis: calc((100% - 90px) / 3);
    }
}
.contacts__item:not(:last-child) {
    @include mq(mobile-only) {
        margin-bottom: 20px;
    }
}
.contacts__item-type {
    display: inline-block;
    padding-left: 24px;
    padding-right: 24px;
    margin-bottom: 25px;
    min-height: 16px;
    @include font (12px, 1.86, 500);
    letter-spacing: 0.04em;
    text-align: center;
    color: var(--primary-white-color);
    border-radius: 4px;
    background-color: #F0D1A5;
    text-transform: uppercase;
    @include mq(tablet) {
        padding-left: 15px;
        padding-right: 15px;  
        @include font (16px, 1.86, 500); 
    }
    

}
.contacts__item-type--green {
    background-color: #C2E297;
    padding-left: 27px;
    padding-right: 27px;
    @include mq(tablet) {
        padding-left: 15px;
        padding-right: 15px;
        height: 28px;
    }
}
.contacts__item-wrapper:after, .contacts__schedule-list:after  {
    content: "";
    display: block;
    margin-top: 25px;
    margin-bottom: 25px;
    width: 100%;
    background-color: #E1E1E1;
    height: 1px;
}
.contacts__item-title, .contacts__schedule-item-title  {
    @include font (12px, 1.86, 500);
    letter-spacing: 0.04em;
    color: var(--black-text-color);
    @include mq(tablet) {
        @include font (14px, 1.86, 500);
    }
    @include mq(desktop) {
        @include font (16px, 1.86, 500);
    }
}
.contacts__item-title {
    margin-bottom: 10px;
}
.contacts__item-dscrp {
    @include font (12px, 1.5, 500);
    color: var(--secondary-text-color);
    @include mq(tablet) {
        @include font (14px, 1.66, 500);
    }
    @include mq(desktop) {
        @include font (16px, 1.83, 500);
    }
}
.contacts__schedule-item {
    margin-bottom: 10px;
}

.contacts__schedule-item-dscrp {
    @include font (12px, 1.86, 500);
    color: var(--pre-title-color);
    @include mq(tablet) {
        @include font (14px, 1.86, 500);
    }
    @include mq(desktop) {
        @include font (16px, 1.86, 500);
    }
}
.contacts__contact-list-dscrp {
    @include font (12px, 1.5, 500);
    color: var(--secondary-text-color);
    @include mq(tablet) {
        @include font (14px, 1.83, 500);
    }
    @include mq(desktop) {
        @include font (16px, 1.83, 500);
    }
}
.button-list {
    display: flex;
    justify-content: center;
    align-items: center;
    @include mq(mobile-only) {
        margin-bottom: 10px;
        flex-direction: column;
    }
    @include mq(tablet) {
        flex-direction: row;
        
    }
}
.button-item:not(:last-child) {
    @include mq(mobile-only) {
        margin-bottom: 10px;
    }
    @include mq(tablet) {
        margin-right: 15px;
    }
}
.btn {
    // font-family: inherit;
    min-height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    @include font (16px, 1.3, 700);
    border:none;
    border-radius: 22px;
    
    @include mq(mobile-only) {
        width:161px;
    }
    @include mq(tablet) {
        padding: 0 30px;
    }
    &--accent {
        color: var(--primary-white-color);
        background-color: var(--accent-color);
        transition: background-color var(--transition-duration) var(--timing-function);
        &:hover, &:focus {
            background-color: var(--btn-focus-color);
        }
    }
    &--light {
        color: var(--accent-color);
        background-color: var(--primary-white-color);
        transition: background-color var(--transition-duration) var(--timing-function);
        &:hover, &:focus {
            background-color: var(--btn-bg-hover);
        }  
    }
    &__icon {
        --color3: #ffffff;
        margin-left: 13px;
        &--light {
            @include mq(mobile-only) {
                margin-left: 8px;
            }
        }
        &--accent {
            --color3: var(--accent-color);
        }
    }
  
}